export default {
  setup(props) {
    // console.log(props);
    // 通过props进行传值，判断如果数据拿不到，就获取sessionStorage中的
    props.playlist.creator = ""; // 对播放量的处理

    function changeCount(num) {
      if (num >= 100000000) {
        return (num / 100000000).toFixed(1) + "亿";
      } else if (num >= 10000) {
        return (num / 10000).toFixed(1) + "万";
      }
    }

    return {
      changeCount
    };
  },

  props: ["playlist"]
};